.text-field__container{position:relative}.text-field__title{margin-bottom:5px}.text-field{width:100%;height:44px;border:1px solid rgba(31,32,65,.25);border-radius:4px;padding:0 15px;color:rgba(31,32,65,.75)}.text-field::-webkit-input-placeholder{opacity:1;color:rgba(31,32,65,.25);-webkit-transition:opacity .2s ease-in-out;transition:opacity .2s ease-in-out}.text-field::-moz-placeholder{opacity:1;color:rgba(31,32,65,.25);-moz-transition:opacity .2s ease-in-out;transition:opacity .2s ease-in-out}.text-field:focus{border-color:rgba(31,32,65,.5);transition:all .2s ease-in-out}.text-field:focus::-webkit-input-placeholder{opacity:0}.text-field:focus::-moz-placeholder{opacity:0}