body{display:flex;flex-direction:column;background:#fff;font-size:14px;line-height:1.7;font-family:montserrat,sans-serif;color:rgba(31,32,65,.75)}.wrapper{min-width:320px;max-width:1920px;margin:0 auto;padding:0 150px}.heading{font-weight:700;color:#1f2041}.heading--level1{font-size:24px;line-height:30px}.heading--level2{font-size:19px;line-height:24px}.heading--level3{font-size:12px;line-height:15px;text-transform:uppercase}.paragraph{font-size:14px;line-height:24px;color:rgba(31,32,65,.75)}.list>*{position:relative;margin:0 0 10px 20px}.list>:before{content:"";position:absolute;top:7px;left:-20px;width:10px;height:10px;border-radius:5px;background:rgba(31,32,65,.25)}@font-face{font-family:montserrat;src:url(../fonts/montserrat-400-normal-544a7.woff2) format("woff2"),url(../fonts/montserrat-400-normal-dbeb0.woff) format("woff"),url(../fonts/montserrat-400-normal-58bac.ttf) format("truetype");font-weight:400;font-style:normal}@font-face{font-family:montserrat;src:url(../fonts/montserrat-700-normal-366cf.woff2) format("woff2"),url(../fonts/montserrat-700-normal-f0de9.woff) format("woff"),url(../fonts/montserrat-700-normal-bfc63.ttf) format("truetype");font-weight:700;font-style:normal}